<template>
    <div class="profile-container">
        <el-card>
            <div slot="header">
                <span></span>
            </div>

            <el-row :gutter="20">
                <el-col :span="12">
                    <el-card class="profile-card">
                        <div slot="header">
                            <span></span>
                        </div>

                        <el-descriptions :column="1" border>
                            <el-descriptions-item label="ID">{{ user.id }}</el-descriptions-item>
                            <el-descriptions-item label="">{{ user.username }}</el-descriptions-item>
                            <el-descriptions-item label="">{{ user.email }}</el-descriptions-item>
                            <el-descriptions-item label="">
                                <el-tag :type="getRoleTagType(user.role)">{{ getRoleText(user.role) }}</el-tag>
                            </el-descriptions-item>
                        </el-descriptions>
                    </el-card>
                </el-col>

                <el-col :span="12">
                    <el-card class="password-card">
                        <div slot="header">
                            <span></span>
                        </div>

                        <el-form :model="passwordForm" :rules="passwordRules" ref="passwordForm" label-width="100px">
                            <el-form-item label="" prop="oldPassword">
                                <el-input v-model="passwordForm.oldPassword" type="password" show-password></el-input>
                            </el-form-item>
                            <el-form-item label="" prop="newPassword">
                                <el-input v-model="passwordForm.newPassword" type="password" show-password></el-input>
                            </el-form-item>
                            <el-form-item label="" prop="confirmPassword">
                                <el-input v-model="passwordForm.confirmPassword" type="password" show-password></el-input>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" @click="handleChangePassword" :loading="passwordLoading">
                                    
                                </el-button>
                                <el-button @click="resetPasswordForm"></el-button>
                            </el-form-item>
                        </el-form>
                    </el-card>
                </el-col>
            </el-row>

            <!--  -->
            <el-card class="permission-card" style="margin-top: 20px;">
                <div slot="header">
                    <span></span>
                </div>

                <div class="permission-content">
                    <div v-if="isAdmin" class="permission-item">
                        <i class="el-icon-user-solid permission-icon admin"></i>
                        <div class="permission-text">
                            <h4></h4>
                            <p></p>
                            <ul>
                                <li></li>
                                <li></li>
                                <li></li>
                                <li></li>
                            </ul>
                        </div>
                    </div>

                    <div v-else-if="isOfficer" class="permission-item">
                        <i class="el-icon-s-custom permission-icon officer"></i>
                        <div class="permission-text">
                            <h4></h4>
                            <p></p>
                            <ul>
                                <li></li>
                                <li></li>
                                <li></li>
                                <li></li>
                            </ul>
                        </div>
                    </div>

                    <div v-else class="permission-item">
                        <i class="el-icon-view permission-icon guest"></i>
                        <div class="permission-text">
                            <h4></h4>
                            <p></p>
                            <ul>
                                <li></li>
                                <li></li>
                                <li></li>
                            </ul>
                            <p class="upgrade-hint"></p>
                        </div>
                    </div>
                </div>
            </el-card>

            <!--  -->
            <el-card class="stats-card" style="margin-top: 20px;" v-if="isOfficer">
                <div slot="header">
                    <span></span>
                </div>

                <el-row :gutter="20">
                    <el-col :span="8">
                        <div class="stat-item">
                            <div class="stat-number">{{ accountCount }}</div>
                            <div class="stat-label"></div>
                        </div>
                    </el-col>
                    <el-col :span="8">
                        <div class="stat-item">
                            <div class="stat-number">{{ transactionCount }}</div>
                            <div class="stat-label"></div>
                        </div>
                    </el-col>
                </el-row>
            </el-card>
        </el-card>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { userApi, accountApi, transactionApi } from '../services/api'

export default {
  name: 'Profile',
  data () {
    // 
    const validateConfirmPassword = (rule, value, callback) => {
      if (value !== this.passwordForm.newPassword) {
        callback(new Error(''))
      } else {
        callback()
      }
    }

    return {
      passwordForm: {
        oldPassword: '',
        newPassword: '',
        confirmPassword: ''
      },
      passwordRules: {
        oldPassword: [
          { required: true, message: '', trigger: 'blur' }
        ],
        newPassword: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, message: '6', trigger: 'blur' }
        ],
        confirmPassword: [
          { required: true, message: '', trigger: 'blur' },
          { validator: validateConfirmPassword, trigger: 'blur' }
        ]
      },
      passwordLoading: false,
      accountCount: 0,
      transactionCount: 0
    }
  },
  computed: {
    ...mapGetters([
      'currentUser',
      'isAdmin',
      'isOfficer',
      'isGuest'
    ]),
    user () {
      return this.currentUser || {}
    }
  },
  created () {
    this.loadUserStats()
  },
  methods: {
    async loadUserStats () {
      if (this.isOfficer) {
        try {
          // 
          const accountResponse = await accountApi.getUserAccounts()
          if (accountResponse.data.code === 200) {
            this.accountCount = accountResponse.data.data.length
          }

          // 
          try {
            const transactionResponse = await transactionApi.getTransactionStats()
            if (transactionResponse.data.code === 200) {
              this.transactionCount = transactionResponse.data.data.count || 0
            }
          } catch (error) {
            console.error(':', error)
            this.transactionCount = 0
          }
        } catch (error) {
          console.error(':', error)
        }
      }
    },

    async handleChangePassword () {
      this.$refs.passwordForm.validate(async valid => {
        if (!valid) {
          return false
        }

        this.passwordLoading = true

        try {
          const response = await userApi.changePassword({
            oldPassword: this.passwordForm.oldPassword,
            newPassword: this.passwordForm.newPassword
          })

          if (response.data.code === 200) {
            this.$message.success('')
            this.resetPasswordForm()
          } else {
            throw new Error(response.data.message || '')
          }
        } catch (error) {
          console.error(':', error)
          let errorMsg = ''
          if (error.response && error.response.data) {
            errorMsg = error.response.data.message || errorMsg
          } else if (error.message) {
            errorMsg = error.message
          }
          this.$message.error(errorMsg)
        } finally {
          this.passwordLoading = false
        }
      })
    },

    resetPasswordForm () {
      this.passwordForm = {
        oldPassword: '',
        newPassword: '',
        confirmPassword: ''
      }
      this.$refs.passwordForm && this.$refs.passwordForm.resetFields()
    },

    getRoleText (role) {
      const roleMap = {
        admin: '',
        officer: '',
        guest: ''
      }
      return roleMap[role] || role
    },

    getRoleTagType (role) {
      const typeMap = {
        admin: 'danger',
        officer: 'warning',
        guest: 'info'
      }
      return typeMap[role] || 'info'
    }
  }
}
</script>

