<template>
    <div class="users-container">
        <el-card>
            <div slot="header" class="clearfix">
                <span></span>
                <el-button style="float: right; padding: 3px 0" type="text" @click="refreshUserList">
                    
                </el-button>
            </div>

            <el-table :data="userList" v-loading="loading" style="width: 100%">
                <el-table-column prop="id" label="ID" width="80"></el-table-column>
                <el-table-column prop="username" label="" width="150"></el-table-column>
                <el-table-column prop="email" label="" width="200"></el-table-column>
                <el-table-column prop="role" label="" width="120">
                    <template slot-scope="scope">
                        <el-tag :type="getRoleTagType(scope.row.role)">{{ getRoleText(scope.row.role) }}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-select v-model="scope.row.newRole" placeholder="" size="small" style="width: 120px">
                            <el-option label="" value="admin"></el-option>
                            <el-option label="" value="officer"></el-option>
                            <el-option label="" value="guest"></el-option>
                        </el-select>
                        <el-button type="primary" size="small" :disabled="scope.row.role === scope.row.newRole"
                            @click="updateUserRole(scope.row)" style="margin-left: 10px">
                            
                        </el-button>
                        <el-button type="warning" size="small" @click="showEditUserDialog(scope.row)" style="margin-left: 5px">
                            
                        </el-button>
                        <el-button type="danger" size="small" @click="deleteUser(scope.row)" style="margin-left: 5px">
                            
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>

            <div v-if="userList.length === 0 && !loading" class="empty-state">
                <p></p>
                <el-button type="primary" size="small" @click="refreshUserList"></el-button>
            </div>
        </el-card>

        <!--  -->
        <el-dialog title="" :visible.sync="editDialogVisible" width="400px">
            <el-form :model="editForm" :rules="editRules" ref="editForm" label-width="80px">
                <el-form-item label="" prop="username">
                    <el-input v-model="editForm.username"></el-input>
                </el-form-item>
                <el-form-item label="" prop="newPassword">
                    <el-input v-model="editForm.newPassword" type="password" placeholder=""></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="editDialogVisible = false"> </el-button>
                <el-button type="primary" @click="handleEditUser" :loading="editLoading"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import { userApi } from '../services/api'

export default {
  name: 'Users',
  data () {
    return {
      userList: [],
      loading: false,
      editDialogVisible: false,
      editLoading: false,
      editForm: {
        id: null,
        username: '',
        newPassword: ''
      },
      editRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 20, message: '3-20', trigger: 'blur' }
        ],
        newPassword: [
          { min: 6, message: '6', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.fetchUserList()
  },
  methods: {
    async fetchUserList () {
      this.loading = true
      try {
        const response = await userApi.getUsers()
        if (response.data.code === 200) {
          this.userList = response.data.data.map(user => ({
            ...user,
            newRole: user.role
          }))
        } else {
          throw new Error(response.data.message || '')
        }

        if (this.userList.length === 0) {
          this.$message.warning('')
        }
      } catch (error) {
        console.error(':', error)
        let errorMsg = ''
        if (error.response && error.response.data) {
          errorMsg = error.response.data.message || errorMsg
        } else if (error.message) {
          errorMsg = error.message
        }
        this.$message.error(errorMsg)
      } finally {
        this.loading = false
      }
    },

    async updateUserRole (user) {
      try {
        const response = await userApi.updateUserRole(user.id, user.newRole)
        if (response.data.code === 200) {
          this.$message.success(` ${user.username}  ${this.getRoleText(user.newRole)}`)
          // 
          user.role = user.newRole
        } else {
          throw new Error(response.data.message || '')
        }
      } catch (error) {
        console.error(':', error)
        let errorMsg = ''
        if (error.response && error.response.data) {
          errorMsg = error.response.data.message || errorMsg
        }
        this.$message.error(errorMsg)
        // 
        user.newRole = user.role
      }
    },

    showEditUserDialog (user) {
      this.editForm = {
        id: user.id,
        username: user.username,
        newPassword: ''
      }
      this.editDialogVisible = true
    },

    async handleEditUser () {
      this.$refs.editForm.validate(async valid => {
        if (!valid) {
          return false
        }

        this.editLoading = true

        try {
          // 
          const usernameResponse = await userApi.updateUsername(this.editForm.id, this.editForm.username)
          if (usernameResponse.data.code !== 200) {
            throw new Error(usernameResponse.data.message || '')
          }

          // 
          if (this.editForm.newPassword) {
            const passwordResponse = await userApi.adminUpdatePassword(this.editForm.id, this.editForm.newPassword)
            if (passwordResponse.data.code !== 200) {
              throw new Error(passwordResponse.data.message || '')
            }
          }

          this.$message.success('')
          this.editDialogVisible = false
          this.fetchUserList() // 
        } catch (error) {
          console.error(':', error)
          let errorMsg = ''
          if (error.response && error.response.data) {
            errorMsg = error.response.data.message || errorMsg
          } else if (error.message) {
            errorMsg = error.message
          }
          this.$message.error(errorMsg)
        } finally {
          this.editLoading = false
        }
      })
    },

    async deleteUser (user) {
      try {
        const confirm = await this.$confirm(
                    ` "${user.username}" `,
                    '',
                    {
                      confirmButtonText: '',
                      cancelButtonText: '',
                      type: 'warning'
                    }
        )

        if (confirm) {
          const response = await userApi.deleteUser(user.id)
          if (response.data.code === 200) {
            this.$message.success('')
            this.fetchUserList() // 
          } else {
            throw new Error(response.data.message || '')
          }
        }
      } catch (error) {
        if (error !== 'cancel') {
          console.error(':', error)
          let errorMsg = ''
          if (error.response && error.response.data) {
            errorMsg = error.response.data.message || errorMsg
          } else if (error.message) {
            errorMsg = error.message
          }
          this.$message.error(errorMsg)
        }
      }
    },

    refreshUserList () {
      this.fetchUserList()
    },

    getRoleText (role) {
      const roleMap = {
        admin: '',
        officer: '',
        guest: ''
      }
      return roleMap[role] || role
    },

    getRoleTagType (role) {
      const typeMap = {
        admin: 'danger',
        officer: 'warning',
        guest: 'info'
      }
      return typeMap[role] || 'info'
    }
  }
}
</script>

<style scoped>
.users-container {
    padding: 20px;
}

.empty-state {
    text-align: center;
    padding: 40px 0;
    color: #606266;
}

.dialog-footer {
    text-align: right;
}
</style>
