<template>
    <div class="transactions-container">
        <el-card>
            <div slot="header" class="clearfix">
                <span></span>
                <el-button style="float: right; padding: 3px 0" type="text" @click="fetchTransactions">
                    
                </el-button>
            </div>

            <!--  -->
            <div class="action-area">
                <el-row :gutter="20">
                    <el-col :span="18">
                        <span class="transaction-summary"> {{ transactionList.length }} </span>
                    </el-col>
                    <el-col :span="6" style="text-align: right;">
                        <el-button type="primary" @click="showCreateTransactionDialog">
                            <i class="el-icon-plus"></i> 
                        </el-button>
                    </el-col>
                </el-row>
            </div>

            <!--  -->
            <el-table :data="transactionList" v-loading="loading" style="width: 100%">
                <el-table-column prop="id" label="ID" width="80"></el-table-column>
                <el-table-column prop="fromAddress" label="" width="200">
                    <template slot-scope="scope">
                        <span :title="scope.row.fromAddress">{{ formatAddress(scope.row.fromAddress) }}</span>
                    </template>
                </el-table-column>
                <el-table-column prop="toAddress" label="" width="200">
                    <template slot-scope="scope">
                        <span :title="scope.row.toAddress">{{ formatAddress(scope.row.toAddress) }}</span>
                    </template>
                </el-table-column>
                <el-table-column prop="amount" label="" width="120">
                    <template slot-scope="scope">
                        {{ scope.row.amount }}
                    </template>
                </el-table-column>
                <el-table-column prop="status" label="" width="100">
                    <template slot-scope="scope">
                        <el-tag :type="getStatusTagType(scope.row.status)">{{ getStatusText(scope.row.status) }}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column prop="txHash" label="" width="200">
                    <template slot-scope="scope">
                        <span v-if="scope.row.txHash" :title="scope.row.txHash">{{ formatAddress(scope.row.txHash) }}</span>
                        <span v-else>-</span>
                    </template>
                </el-table-column>
                <el-table-column prop="createdAt" label="" width="160">
                    <template slot-scope="scope">
                        {{ formatDateTime(scope.row.createdAt) }}
                    </template>
                </el-table-column>
                <el-table-column label="" width="220">
                    <template slot-scope="scope">
                        <el-button v-if="scope.row.status === 'prepared'" type="warning" size="mini" @click="signTransaction(scope.row)">
                            
                        </el-button>
                        <el-button type="primary" size="mini" @click="viewTransactionDetail(scope.row)">
                            
                        </el-button>
                        <el-button type="danger" size="mini" @click="deleteTransaction(scope.row)" style="margin-left: 5px">
                            
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>

            <div v-if="transactionList.length === 0 && !loading" class="empty-state">
                <p></p>
                <el-button type="primary" size="small" @click="showCreateTransactionDialog"></el-button>
            </div>
        </el-card>

        <!--  -->
        <el-dialog title="" :visible.sync="createDialogVisible" width="500px">
            <el-form :model="createForm" :rules="createRules" ref="createForm" label-width="100px">
                <el-form-item label="" prop="fromAddress">
                    <el-select v-model="createForm.fromAddress" placeholder="" style="width: 100%">
                        <el-option
                            v-for="account in userAccounts"
                            :key="account.address"
                            :label="account.address"
                            :value="account.address">
                            <span>{{ formatAddress(account.address) }}</span>
                            <span style="float: right; color: #8492a6; font-size: 13px">{{ account.balance || '0' }} {{ account.coinType }}</span>
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <span v-if="selectedAccountBalance">{{ selectedAccountBalance }} ETH</span>
                    <span v-else></span>
                    <el-button v-if="createForm.fromAddress" type="text" size="mini" @click="refreshSelectedBalance"></el-button>
                </el-form-item>
                <el-form-item label="" prop="toAddress">
                    <el-input v-model="createForm.toAddress" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="amount">
                    <el-input-number
                        v-model="createForm.amount"
                        :precision="6"
                        :step="0.001"
                        :min="0.001"
                        style="width: 100%">
                    </el-input-number>
                    <span style="margin-left: 10px;">ETH</span>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="createDialogVisible = false"> </el-button>
                <el-button type="primary" @click="handleCreateTransaction" :loading="createLoading"></el-button>
            </div>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="signDialogVisible" width="500px">
            <div v-if="selectedTransaction" class="sign-transaction">
                <el-descriptions :column="1" border>
                    <el-descriptions-item label="ID">{{ selectedTransaction.id }}</el-descriptions-item>
                    <el-descriptions-item label="">{{ selectedTransaction.fromAddress }}</el-descriptions-item>
                    <el-descriptions-item label="">{{ selectedTransaction.toAddress }}</el-descriptions-item>
                    <el-descriptions-item label="">{{ selectedTransaction.amount }} ETH</el-descriptions-item>
                    <el-descriptions-item label="">{{ selectedTransaction.messageHash }}</el-descriptions-item>
                </el-descriptions>

                <el-form :model="signForm" :rules="signRules" ref="signForm" label-width="100px" style="margin-top: 20px;">
                    <el-form-item label="" prop="signature">
                        <el-input v-model="signForm.signature" type="textarea" :rows="3" placeholder=""></el-input>
                    </el-form-item>
                </el-form>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button @click="signDialogVisible = false"> </el-button>
                <el-button type="primary" @click="handleSignTransaction" :loading="signLoading"></el-button>
            </div>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="detailDialogVisible" width="600px">
            <div v-if="selectedTransaction" class="transaction-detail">
                <el-descriptions :column="1" border>
                    <el-descriptions-item label="ID">{{ selectedTransaction.id }}</el-descriptions-item>
                    <el-descriptions-item label="">
                        <div class="address-row">
                            <span class="address-text">{{ selectedTransaction.fromAddress }}</span>
                            <div class="address-actions">
                                <el-button type="text" size="mini" @click="copyText(selectedTransaction.fromAddress)">
                                    <i class="el-icon-document-copy"></i> 
                                </el-button>
                            </div>
                        </div>
                    </el-descriptions-item>
                    <el-descriptions-item label="">
                        <div class="address-row">
                            <span class="address-text">{{ selectedTransaction.toAddress }}</span>
                            <div class="address-actions">
                                <el-button type="text" size="mini" @click="copyText(selectedTransaction.toAddress)">
                                    <i class="el-icon-document-copy"></i> 
                                </el-button>
                            </div>
                        </div>
                    </el-descriptions-item>
                    <el-descriptions-item label="">{{ selectedTransaction.amount }}</el-descriptions-item>
                    <el-descriptions-item label="">
                        <el-tag :type="getStatusTagType(selectedTransaction.status)">{{ getStatusText(selectedTransaction.status) }}</el-tag>
                    </el-descriptions-item>
                    <el-descriptions-item label="" v-if="selectedTransaction.messageHash">
                        <div class="hash-row">
                            <span class="hash-text">{{ selectedTransaction.messageHash }}</span>
                            <div class="hash-actions">
                                <el-button type="text" size="mini" @click="copyText(selectedTransaction.messageHash)">
                                    <i class="el-icon-document-copy"></i> 
                                </el-button>
                            </div>
                        </div>
                    </el-descriptions-item>
                    <el-descriptions-item label="" v-if="selectedTransaction.txHash">
                        <div class="hash-row">
                            <span class="hash-text">{{ selectedTransaction.txHash }}</span>
                            <div class="hash-actions">
                                <el-button type="text" size="mini" @click="copyText(selectedTransaction.txHash)">
                                    <i class="el-icon-document-copy"></i> 
                                </el-button>
                            </div>
                        </div>
                    </el-descriptions-item>
                    <el-descriptions-item label="">{{ formatDateTime(selectedTransaction.createdAt) }}</el-descriptions-item>
                    <el-descriptions-item label="" v-if="selectedTransaction.updatedAt">{{ formatDateTime(selectedTransaction.updatedAt) }}</el-descriptions-item>
                </el-descriptions>

                <!--  -->
                <div class="detail-actions">
                    <el-button type="primary" @click="downloadTransactionInfo">
                        <i class="el-icon-download"></i> 
                    </el-button>
                    <el-button @click="copyAllTransactionInfo">
                        <i class="el-icon-document-copy"></i> 
                    </el-button>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import { transactionApi, accountApi } from '../services/api'

export default {
  name: 'Transactions',
  data () {
    return {
      transactionList: [],
      userAccounts: [],
      loading: false,
      searchFromAddress: '',
      searchToAddress: '',
      statusFilter: '',
      createDialogVisible: false,
      signDialogVisible: false,
      detailDialogVisible: false,
      createLoading: false,
      signLoading: false,
      selectedTransaction: null,
      selectedAccountBalance: '',
      createForm: {
        fromAddress: '',
        toAddress: '',
        amount: null
      },
      createRules: {
        fromAddress: [
          { required: true, message: '', trigger: 'change' }
        ],
        toAddress: [
          { required: true, message: '', trigger: 'blur' },
          { min: 10, message: '10', trigger: 'blur' }
        ],
        amount: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'number', min: 0.000001, message: '0', trigger: 'blur' }
        ]
      },
      signForm: {
        signature: ''
      },
      signRules: {
        signature: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.fetchUserAccounts()
    this.fetchTransactions()
  },
  methods: {
    async fetchUserAccounts () {
      try {
        let response

        // 
        if (this.$store.getters.isAdmin) {
          response = await accountApi.getAllAccounts()
        } else {
          response = await accountApi.getUserAccounts()
        }

        if (response.data.code === 200) {
          const accountsData = response.data.data
          let rawAccounts = []

          if (Array.isArray(accountsData)) {
            rawAccounts = accountsData
          } else if (accountsData && Array.isArray(accountsData.accounts)) {
            rawAccounts = accountsData.accounts
          } else {
            rawAccounts = []
          }

          // 
          this.userAccounts = rawAccounts.map(account => ({
            id: account.ID,
            address: account.Address,
            coinType: account.CoinType,
            balance: account.Balance,
            importedBy: account.ImportedBy,
            description: account.Description,
            createdAt: account.CreatedAt,
            updatedAt: account.UpdatedAt
          }))
        }
      } catch (error) {
        console.error(':', error)
      }
    },

    async fetchTransactions () {
      this.loading = true
      try {
        // 
        const params = {}
        if (this.searchFromAddress.trim()) {
          params.fromAddress = this.searchFromAddress.trim()
        }
        if (this.searchToAddress.trim()) {
          params.toAddress = this.searchToAddress.trim()
        }
        if (this.statusFilter) {
          params.status = this.statusFilter
        }

        let response

        // 
        if (this.$store.getters.isAdmin) {
          response = await transactionApi.getAllTransactions(params)
        } else {
          response = await transactionApi.getTransactions(params)
        }

        if (response.data.code === 200) {
          this.transactionList = response.data.data.transactions || response.data.data || []
        } else {
          throw new Error(response.data.message || '')
        }
      } catch (error) {
        console.error(':', error)
        let errorMsg = ''
        if (error.response && error.response.data) {
          errorMsg = error.response.data.message || errorMsg
        } else if (error.message) {
          errorMsg = error.message
        }
        this.$message.error(errorMsg)
        // API
        this.transactionList = []
      } finally {
        this.loading = false
      }
    },

    searchTransactions () {
      // 
      this.fetchTransactions()
    },

    resetSearch () {
      this.searchFromAddress = ''
      this.searchToAddress = ''
      this.statusFilter = ''
      this.fetchTransactions()
    },

    showCreateTransactionDialog () {
      this.createForm = {
        fromAddress: '',
        toAddress: '',
        amount: null
      }
      this.selectedAccountBalance = ''
      this.createDialogVisible = true
    },

    async handleCreateTransaction () {
      this.$refs.createForm.validate(async valid => {
        if (!valid) {
          return false
        }

        this.createLoading = true

        try {
          const response = await transactionApi.prepareTransaction({
            fromAddress: this.createForm.fromAddress,
            toAddress: this.createForm.toAddress,
            amount: this.createForm.amount
          })

          if (response.data.code === 200) {
            this.$message.success('')
            this.createDialogVisible = false
            this.fetchTransactions()
          } else {
            throw new Error(response.data.message || '')
          }
        } catch (error) {
          console.error(':', error)
          let errorMsg = ''
          if (error.response && error.response.data) {
            errorMsg = error.response.data.message || errorMsg
          } else if (error.message) {
            errorMsg = error.message
          }
          this.$message.error(errorMsg)
        } finally {
          this.createLoading = false
        }
      })
    },

    signTransaction (transaction) {
      this.selectedTransaction = transaction
      this.signForm.signature = ''
      this.signDialogVisible = true
    },

    async handleSignTransaction () {
      this.$refs.signForm.validate(async valid => {
        if (!valid) {
          return false
        }

        this.signLoading = true

        try {
          const response = await transactionApi.signAndSendTransaction({
            messageHash: this.selectedTransaction.messageHash,
            signature: this.signForm.signature
          })

          if (response.data.code === 200) {
            this.$message.success('')
            this.signDialogVisible = false
            this.fetchTransactions()
          } else {
            throw new Error(response.data.message || '')
          }
        } catch (error) {
          console.error(':', error)
          let errorMsg = ''
          if (error.response && error.response.data) {
            errorMsg = error.response.data.message || errorMsg
          } else if (error.message) {
            errorMsg = error.message
          }
          this.$message.error(errorMsg)
        } finally {
          this.signLoading = false
        }
      })
    },

    viewTransactionDetail (transaction) {
      this.selectedTransaction = transaction
      this.detailDialogVisible = true
    },

    async refreshSelectedBalance () {
      if (!this.createForm.fromAddress) return

      try {
        const response = await transactionApi.getBalance(this.createForm.fromAddress)
        if (response.data.code === 200) {
          this.selectedAccountBalance = response.data.data.balance
        }
      } catch (error) {
        console.error(':', error)
        this.$message.error('')
      }
    },

    copyText (text) {
      if (navigator.clipboard) {
        navigator.clipboard.writeText(text).then(() => {
          this.$message.success('')
        }).catch(() => {
          this.fallbackCopyTextToClipboard(text)
        })
      } else {
        this.fallbackCopyTextToClipboard(text)
      }
    },

    fallbackCopyTextToClipboard (text) {
      const textArea = document.createElement('textarea')
      textArea.value = text
      document.body.appendChild(textArea)
      textArea.focus()
      textArea.select()
      try {
        document.execCommand('copy')
        this.$message.success('')
      } catch (err) {
        this.$message.error('')
      }
      document.body.removeChild(textArea)
    },

    formatAddress (address) {
      if (!address || address.length <= 20) return address
      return `${address.slice(0, 10)}...${address.slice(-8)}`
    },

    formatDateTime (dateString) {
      if (!dateString) return '-'
      return new Date(dateString).toLocaleString('zh-CN')
    },

    getStatusText (status) {
      const statusMap = {
        prepared: '',
        signed: '',
        sent: '',
        confirmed: '',
        failed: ''
      }
      return statusMap[status] || status
    },

    getStatusTagType (status) {
      const typeMap = {
        prepared: 'warning',
        signed: 'primary',
        sent: 'info',
        confirmed: 'success',
        failed: 'danger'
      }
      return typeMap[status] || 'info'
    },

    // 
    copyAllTransactionInfo () {
      if (!this.selectedTransaction) return

      const info = this.formatTransactionInfo(this.selectedTransaction)
      this.copyText(info)
    },

    // 
    downloadTransactionInfo () {
      if (!this.selectedTransaction) return

      const info = this.formatTransactionInfo(this.selectedTransaction)
      const filename = `transaction_${this.selectedTransaction.id}_${new Date().getTime()}.txt`

      // 
      const blob = new Blob([info], { type: 'text/plain;charset=utf-8' })
      const url = window.URL.createObjectURL(blob)
      const link = document.createElement('a')
      link.href = url
      link.download = filename
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
      window.URL.revokeObjectURL(url)

      this.$message.success('')
    },

    // 
    async deleteTransaction (transaction) {
      try {
        const confirm = await this.$confirm(
          ` ID "${transaction.id}" `,
          '',
          {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }
        )

        if (confirm) {
          const response = await transactionApi.deleteTransaction(transaction.id)
          if (response.data.code === 200) {
            this.$message.success('')
            this.fetchTransactions() // 
          } else {
            throw new Error(response.data.message || '')
          }
        }
      } catch (error) {
        if (error !== 'cancel') {
          console.error(':', error)
          let errorMsg = ''
          if (error.response && error.response.data) {
            errorMsg = error.response.data.message || errorMsg
          } else if (error.message) {
            errorMsg = error.message
          }
          this.$message.error(errorMsg)
        }
      }
    },

    // 
    formatTransactionInfo (transaction) {
      let info = '\n'
      info += '================\n\n'
      info += `ID: ${transaction.id}\n`
      info += `: ${transaction.fromAddress}\n`
      info += `: ${transaction.toAddress}\n`
      info += `: ${transaction.amount}\n`
      info += `: ${this.getStatusText(transaction.status)}\n`
      if (transaction.messageHash) {
        info += `: ${transaction.messageHash}\n`
      }
      if (transaction.txHash) {
        info += `: ${transaction.txHash}\n`
      }
      info += `: ${this.formatDateTime(transaction.createdAt)}\n`
      if (transaction.updatedAt) {
        info += `: ${this.formatDateTime(transaction.updatedAt)}\n`
      }
      info += '\n: ' + new Date().toLocaleString('zh-CN')

      return info
    }
  },
  watch: {
    'createForm.fromAddress' () {
      this.refreshSelectedBalance()
    }
  }
}
</script>

<style scoped>
.transactions-container {
    padding: 20px;
}

.action-area {
    margin-bottom: 20px;
    padding: 15px 0;
    border-bottom: 1px solid #f0f0f0;
}

.transaction-summary {
    font-size: 14px;
    color: #666;
}

.empty-state {
    text-align: center;
    padding: 40px 0;
    color: #606266;
}

.dialog-footer {
    text-align: right;
}

.sign-transaction,
.transaction-detail {
    padding: 10px 0;
}

.address-row,
.hash-row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.address-text,
.hash-text {
    flex: 1;
    word-break: break-all;
    margin-right: 10px;
    font-family: 'Courier New', monospace;
    font-size: 13px;
}

.address-actions,
.hash-actions {
    flex-shrink: 0;
}

.detail-actions {
    margin-top: 20px;
    text-align: center;
    padding-top: 15px;
    border-top: 1px solid #f0f0f0;
}

.detail-actions .el-button {
    margin: 0 10px;
}
</style>
