<template>
    <div class="login-container">
        <el-card class="login-card">
            <div slot="header" class="card-header">
                <h2></h2>
            </div>

            <el-form :model="loginForm" :rules="rules" ref="loginForm" label-width="0px">
                <el-form-item prop="username">
                    <el-input v-model="loginForm.username" prefix-icon="el-icon-user" placeholder="">
                    </el-input>
                </el-form-item>

                <el-form-item prop="password">
                    <el-input v-model="loginForm.password" prefix-icon="el-icon-lock" placeholder="" show-password>
                    </el-input>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" :loading="loading" @click="handleLogin" style="width: 100%">
                        
                    </el-button>
                </el-form-item>

                <el-form-item>
                    <div class="register-link">
                        <span>?</span>
                        <router-link to="/register"></router-link>
                    </div>
                </el-form-item>
            </el-form>
        </el-card>
    </div>
</template>

<script>
import { userApi } from '../services/api'

export default {
  name: 'Login',
  data () {
    return {
      loginForm: {
        username: '',
        password: ''
      },
      rules: {
        username: [{ required: true, message: '', trigger: 'blur' }],
        password: [{ required: true, message: '', trigger: 'blur' }]
      },
      loading: false
    }
  },
  methods: {
    handleLogin () {
      this.$refs.loginForm.validate(async valid => {
        if (!valid) {
          return false
        }

        this.loading = true

        try {
          const response = await userApi.login({
            username: this.loginForm.username,
            password: this.loginForm.password
          })

          // 
          if (!response.data || response.data.code !== 200) {
            throw new Error(response.data?.message || '')
          }

          const userData = response.data.data

          // 
          this.$store.dispatch('login', {
            token: userData.token,
            user: userData.user
          })

          console.log('User login successful:', userData.user.username, userData.user.role)

          // 
          this.$router.push('/dashboard')
          this.$message.success('')
        } catch (error) {
          console.error('Login failed:', error)
          let errorMsg = ''

          if (error.response && error.response.data) {
            errorMsg = error.response.data.message || errorMsg
          } else if (error.message) {
            errorMsg = error.message
          }

          this.$message.error(errorMsg)
        } finally {
          this.loading = false
        }
      })
    }
  }
}
</script>

