<template>
    <div class="register-container">
        <el-card class="register-card">
            <div slot="header" class="card-header">
                <h2></h2>
            </div>

            <el-form :model="registerForm" :rules="rules" ref="registerForm" label-width="0px">
                <el-form-item prop="username">
                    <el-input v-model="registerForm.username" prefix-icon="el-icon-user" placeholder="">
                    </el-input>
                </el-form-item>

                <el-form-item prop="password">
                    <el-input v-model="registerForm.password" prefix-icon="el-icon-lock" placeholder="" show-password>
                    </el-input>
                </el-form-item>

                <el-form-item prop="confirmPassword">
                    <el-input v-model="registerForm.confirmPassword" prefix-icon="el-icon-lock" placeholder=""
                        show-password>
                    </el-input>
                </el-form-item>

                <el-form-item prop="email">
                    <el-input v-model="registerForm.email" prefix-icon="el-icon-message" placeholder="">
                    </el-input>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" :loading="loading" @click="handleRegister" style="width: 100%">
                        
                    </el-button>
                </el-form-item>

                <el-form-item>
                    <div class="login-link">
                        <span>?</span>
                        <router-link to="/login"></router-link>
                    </div>
                </el-form-item>
            </el-form>
        </el-card>
    </div>
</template>

<script>
import { userApi } from '../services/api'

export default {
  name: 'Register',
  data () {
    // 
    const validateConfirmPassword = (rule, value, callback) => {
      if (value !== this.registerForm.password) {
        callback(new Error(''))
      } else {
        callback()
      }
    }

    return {
      registerForm: {
        username: '',
        password: '',
        confirmPassword: '',
        email: ''
      },
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 20, message: '3-20', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, message: '6', trigger: 'blur' }
        ],
        confirmPassword: [
          { required: true, message: '', trigger: 'blur' },
          { validator: validateConfirmPassword, trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'email', message: '', trigger: 'blur' }
        ]
      },
      loading: false
    }
  },
  methods: {
    handleRegister () {
      this.$refs.registerForm.validate(async valid => {
        if (!valid) {
          return false
        }

        this.loading = true

        try {
          const response = await userApi.register({
            username: this.registerForm.username,
            password: this.registerForm.password,
            email: this.registerForm.email
          })

          // 
          if (!response.data || response.data.code !== 200) {
            throw new Error(response.data?.message || '')
          }

          // 
          this.$message.success('')

          // 
          this.$router.push('/login')
        } catch (error) {
          console.error('Registration failed:', error)
          let errorMsg = ''

          if (error.response && error.response.data) {
            errorMsg = error.response.data.message || errorMsg
          } else if (error.message) {
            errorMsg = error.message
          }

          this.$message.error(errorMsg)
        } finally {
          this.loading = false
        }
      })
    }
  }
}
</script>

<style scoped>
.register-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f5f7fa;
}

.register-card {
    width: 400px;
    border-radius: 8px;
}

.card-header {
    text-align: center;
}

.card-header h2 {
    margin: 0;
    color: #409EFF;
}

.login-link {
    text-align: center;
    font-size: 14px;
    color: #606266;
}

.login-link a {
    color: #409EFF;
    text-decoration: none;
    margin-left: 5px;
}
</style>
