<template>
    <div class="dashboard-container">
        <el-container>
            <el-header class="header">
                <div class="logo"></div>
                <div class="user-info">
                    <span>, {{ user.username }} ({{ roleText }})</span>
                    <el-dropdown @command="handleCommand">
                        <span class="el-dropdown-link">
                            <i class="el-icon-setting"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command="profile"></el-dropdown-item>
                            <el-dropdown-item command="logout" divided></el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </div>
            </el-header>

            <el-container>
                <el-aside width="250px">
                    <el-menu :default-active="activeMenu" router class="menu" background-color="#304156"
                        text-color="#bfcbd9" active-text-color="#409EFF">
                        <el-menu-item v-if="isAdmin" index="/users">
                            <i class="el-icon-user"></i>
                            <span></span>
                        </el-menu-item>
                        <el-menu-item v-if="isOfficer" index="/accounts">
                            <i class="el-icon-wallet"></i>
                            <span></span>
                        </el-menu-item>
                        <el-menu-item v-if="isOfficer" index="/transactions">
                            <i class="el-icon-s-finance"></i>
                            <span></span>
                        </el-menu-item>
                        <el-menu-item index="/profile">
                            <i class="el-icon-user-solid"></i>
                            <span></span>
                        </el-menu-item>
                    </el-menu>
                </el-aside>

                <el-main>
                    <router-view></router-view>

                    <div v-if="$route.path === '/dashboard'" class="welcome-container">
                        <div class="welcome-message">
                            <i class="el-icon-s-home welcome-icon"></i>
                            <h2></h2>
                            <p></p>

                            <!--  -->
                            <div class="stats-container">
                                <el-row :gutter="20">
                                    <el-col :span="8" v-if="isOfficer">
                                        <el-card class="stat-card">
                                            <div class="stat-content">
                                                <div class="stat-number">{{ accountCount }}</div>
                                                <div class="stat-label"></div>
                                            </div>
                                            <i class="el-icon-wallet stat-icon"></i>
                                        </el-card>
                                    </el-col>
                                    <el-col :span="8" v-if="isOfficer">
                                        <el-card class="stat-card">
                                            <div class="stat-content">
                                                <div class="stat-number">{{ transactionCount }}</div>
                                                <div class="stat-label"></div>
                                            </div>
                                            <i class="el-icon-s-finance stat-icon"></i>
                                        </el-card>
                                    </el-col>
                                    <el-col :span="8" v-if="isAdmin">
                                        <el-card class="stat-card">
                                            <div class="stat-content">
                                                <div class="stat-number">{{ userCount }}</div>
                                                <div class="stat-label"></div>
                                            </div>
                                            <i class="el-icon-user stat-icon"></i>
                                        </el-card>
                                    </el-col>
                                </el-row>
                            </div>

                            <div v-if="isAdmin" class="feature-box">
                                <h3></h3>
                                <el-button type="primary" @click="$router.push('/users')"></el-button>
                                <el-button type="primary" @click="$router.push('/accounts')"></el-button>
                                <el-button type="primary" @click="$router.push('/transactions')"></el-button>
                            </div>

                            <div v-else-if="isOfficer" class="feature-box">
                                <h3></h3>
                                <el-button type="primary" @click="$router.push('/accounts')"></el-button>
                                <el-button type="primary" @click="$router.push('/transactions')"></el-button>
                            </div>

                            <div v-else class="feature-box">
                                <h3></h3>
                                <p></p>
                                <el-button type="primary" @click="$router.push('/profile')"></el-button>
                            </div>
                        </div>
                    </div>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { userApi, accountApi, transactionApi } from '../services/api'

export default {
  name: 'Dashboard',
  data () {
    return {
      activeMenu: this.$route.path,
      accountCount: 0,
      transactionCount: 0,
      userCount: 0
    }
  },
  computed: {
    ...mapGetters([
      'currentUser',
      'isAdmin',
      'isOfficer',
      'isGuest'
    ]),
    user () {
      return this.currentUser || {}
    },
    roleText () {
      if (this.isAdmin) return ''
      if (this.isOfficer) return ''
      return ''
    }
  },
  created () {
    this.loadDashboardStats()
  },
  methods: {
    // 
    async loadDashboardStats () {
      try {
        // 
        if (this.isOfficer) {
          const accountResponse = await accountApi.getUserAccounts()
          if (accountResponse.data.code === 200) {
            this.accountCount = accountResponse.data.data.length
          }
        }

        // 
        if (this.isAdmin) {
          const userResponse = await userApi.getUsers()
          if (userResponse.data.code === 200) {
            this.userCount = userResponse.data.data.length
          }
        }

        // 
        if (this.isOfficer) {
          try {
            let transactionResponse
            if (this.isAdmin) {
              transactionResponse = await transactionApi.getAllTransactionStats()
            } else {
              transactionResponse = await transactionApi.getTransactionStats()
            }

            if (transactionResponse.data.code === 200) {
              this.transactionCount = transactionResponse.data.data.count || 0
            }
          } catch (error) {
            console.error('Failed to load transaction stats:', error)
            // API0
            this.transactionCount = 0
          }
        }
      } catch (error) {
        console.error('Failed to load dashboard stats:', error)
      }
    },

    // 
    handleCommand (command) {
      switch (command) {
        case 'profile':
          this.$router.push('/profile')
          break
        case 'logout':
          this.handleLogout()
          break
      }
    },

    // 
    async handleLogout () {
      try {
        // API
        await userApi.logout()
      } catch (error) {
        console.error('Logout API failed:', error)
        // API
      } finally {
        this.$store.dispatch('logout')
        this.$router.push('/login')
        this.$message.success('')
      }
    }
  },
  watch: {
    // 
    '$route.path' (newPath) {
      this.activeMenu = newPath
    }
  }
}
</script>

