<template>
    <div class="accounts-container">
        <el-card>
            <div slot="header" class="clearfix">
                <span></span>
                <div style="float: right;">
                    <el-button type="primary" size="small" @click="showCreateDialog">
                        
                    </el-button>
                    <el-button type="success" size="small" @click="showImportDialog">
                        
                    </el-button>
                    <el-button type="text" @click="refreshAccountList">
                        
                    </el-button>
                </div>
            </div>

            <!--  -->
            <div class="search-area">
                <el-row :gutter="20">
                    <el-col :span="8">
                        <el-input v-model="searchAddress" placeholder="" clearable>
                            <el-button slot="append" icon="el-icon-search" @click="searchAccount"></el-button>
                        </el-input>
                    </el-col>
                    <el-col :span="4">
                        <el-select v-model="filterCoinType" placeholder="" clearable @change="handleFilter">
                            <el-option label="ETH" value="ETH"></el-option>
                            <el-option label="BTC" value="BTC"></el-option>
                            <el-option label="USDT" value="USDT"></el-option>
                        </el-select>
                    </el-col>
                </el-row>
            </div>

            <el-table :data="filteredAccountList" v-loading="loading" style="width: 100%">
                <el-table-column prop="id" label="ID" width="80"></el-table-column>
                <el-table-column prop="address" label="" width="280">
                    <template slot-scope="scope">
                        <span :title="scope.row.address">{{ formatAddress(scope.row.address) }}</span>
                        <el-button type="text" size="mini" @click="copyAddress(scope.row.address)"></el-button>
                    </template>
                </el-table-column>
                <el-table-column prop="coinType" label="" width="100">
                    <template slot-scope="scope">
                        <el-tag :type="getCoinTypeTagType(scope.row.coinType)">{{ scope.row.coinType }}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column prop="balance" label="" width="150">
                    <template slot-scope="scope">
                        <span>{{ scope.row.balance || '0' }} {{ scope.row.coinType }}</span>
                        <el-button type="text" size="mini" @click="refreshBalance(scope.row)"></el-button>
                    </template>
                </el-table-column>
                <el-table-column prop="importedBy" label="" width="120"></el-table-column>
                <el-table-column prop="description" label="" show-overflow-tooltip></el-table-column>
                <el-table-column label="" width="160">
                    <template slot-scope="scope">
                        <el-button type="primary" size="mini" @click="viewAccountDetail(scope.row)">
                            
                        </el-button>
                        <el-button type="danger" size="mini" @click="deleteAccount(scope.row)" style="margin-left: 5px">
                            
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>

            <div v-if="filteredAccountList.length === 0 && !loading" class="empty-state">
                <p></p>
                <el-button type="primary" size="small" @click="showCreateDialog"></el-button>
            </div>
        </el-card>

        <!--  -->
        <el-dialog title="" :visible.sync="createDialogVisible" width="500px">
            <el-form :model="createForm" :rules="createRules" ref="createForm" label-width="100px">
                <el-form-item label="" prop="address">
                    <el-input v-model="createForm.address" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="coinType">
                    <el-select v-model="createForm.coinType" placeholder="" style="width: 100%">
                        <el-option label="ETH" value="ETH"></el-option>
                        <el-option label="BTC" value="BTC"></el-option>
                        <el-option label="USDT" value="USDT"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="description">
                    <el-input v-model="createForm.description" type="textarea" :rows="3" placeholder=""></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="createDialogVisible = false"> </el-button>
                <el-button type="primary" @click="handleCreateAccount" :loading="createLoading"> </el-button>
            </div>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="importDialogVisible" width="600px">
            <div class="import-area">
                <p></p>
                <p><strong>,,</strong></p>
                <p class="example">0x1234567890abcdef...,ETH,</p>
                <el-input
                    v-model="importText"
                    type="textarea"
                    :rows="8"
                    placeholder="1,1,1&#10;2,2,2&#10;...">
                </el-input>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button @click="importDialogVisible = false"> </el-button>
                <el-button type="primary" @click="handleImportAccounts" :loading="importLoading"> </el-button>
            </div>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="detailDialogVisible" width="500px">
            <div v-if="selectedAccount" class="account-detail">
                <el-descriptions :column="1" border>
                    <el-descriptions-item label="">
                        <span>{{ selectedAccount.address }}</span>
                        <el-button type="text" size="mini" @click="copyAddress(selectedAccount.address)"></el-button>
                    </el-descriptions-item>
                    <el-descriptions-item label="">
                        <el-tag :type="getCoinTypeTagType(selectedAccount.coinType)">{{ selectedAccount.coinType }}</el-tag>
                    </el-descriptions-item>
                    <el-descriptions-item label="">
                        {{ selectedAccount.balance || '0' }} {{ selectedAccount.coinType }}
                    </el-descriptions-item>
                    <el-descriptions-item label="">{{ selectedAccount.importedBy }}</el-descriptions-item>
                    <el-descriptions-item label="">{{ selectedAccount.description || '' }}</el-descriptions-item>
                </el-descriptions>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import { accountApi, transactionApi } from '../services/api'

export default {
  name: 'Accounts',
  data () {
    return {
      accountList: [],
      filteredAccountList: [],
      loading: false,
      searchAddress: '',
      filterCoinType: '',
      createDialogVisible: false,
      importDialogVisible: false,
      detailDialogVisible: false,
      createLoading: false,
      importLoading: false,
      selectedAccount: null,
      importText: '',
      createForm: {
        address: '',
        coinType: '',
        description: ''
      },
      createRules: {
        address: [
          { required: true, message: '', trigger: 'blur' },
          { min: 10, message: '10', trigger: 'blur' }
        ],
        coinType: [
          { required: true, message: '', trigger: 'change' }
        ]
      }
    }
  },
  created () {
    this.fetchAccountList()
  },
  methods: {
    async fetchAccountList () {
      this.loading = true
      try {
        let response

        // 
        if (this.$store.getters.isAdmin) {
          response = await accountApi.getAllAccounts()
        } else {
          response = await accountApi.getUserAccounts()
        }

        if (response.data.code === 200) {
          const accountsData = response.data.data
          let rawAccounts = []

          if (Array.isArray(accountsData)) {
            rawAccounts = accountsData
          } else if (accountsData && Array.isArray(accountsData.accounts)) {
            rawAccounts = accountsData.accounts
          } else {
            rawAccounts = []
          }

          // 
          this.accountList = rawAccounts.map(account => ({
            id: account.ID,
            address: account.Address,
            coinType: account.CoinType,
            balance: account.Balance,
            importedBy: account.ImportedBy,
            description: account.Description,
            createdAt: account.CreatedAt,
            updatedAt: account.UpdatedAt
          }))

          this.filteredAccountList = [...this.accountList]
        } else {
          throw new Error(response.data.message || '')
        }
      } catch (error) {
        console.error(':', error)
        let errorMsg = ''
        if (error.response && error.response.data) {
          errorMsg = error.response.data.message || errorMsg
        } else if (error.message) {
          errorMsg = error.message
        }
        this.$message.error(errorMsg)
      } finally {
        this.loading = false
      }
    },

    searchAccount () {
      if (!this.searchAddress.trim()) {
        this.applyFilters()
        return
      }

      // API
      this.searchAccountByAddress(this.searchAddress.trim())
    },

    async searchAccountByAddress (address) {
      try {
        const response = await accountApi.getAccountByAddress(address)
        if (response.data.code === 200) {
          const account = response.data.data
          // 
          const transformedAccount = {
            id: account.ID,
            address: account.Address,
            coinType: account.CoinType,
            balance: account.Balance,
            importedBy: account.ImportedBy,
            description: account.Description,
            createdAt: account.CreatedAt,
            updatedAt: account.UpdatedAt
          }
          this.filteredAccountList = [transformedAccount]
          this.$message.success('')
        } else {
          this.$message.warning('')
          this.filteredAccountList = []
        }
      } catch (error) {
        console.error(':', error)
        this.$message.error('')
        this.applyFilters()
      }
    },

    handleFilter () {
      this.applyFilters()
    },

    applyFilters () {
      let filtered = [...this.accountList]

      // 
      if (this.filterCoinType) {
        filtered = filtered.filter(account => account.coinType === this.filterCoinType)
      }

      // 
      if (this.searchAddress.trim()) {
        const searchTerm = this.searchAddress.trim().toLowerCase()
        filtered = filtered.filter(account =>
          account.address.toLowerCase().includes(searchTerm)
        )
      }

      this.filteredAccountList = filtered
    },

    showCreateDialog () {
      this.createForm = {
        address: '',
        coinType: '',
        description: ''
      }
      this.createDialogVisible = true
    },

    async handleCreateAccount () {
      this.$refs.createForm.validate(async valid => {
        if (!valid) {
          return false
        }

        this.createLoading = true

        try {
          const response = await accountApi.createAccount({
            address: this.createForm.address,
            coinType: this.createForm.coinType,
            description: this.createForm.description
          })

          if (response.data.code === 200) {
            this.$message.success('')
            this.createDialogVisible = false
            this.fetchAccountList()
          } else {
            throw new Error(response.data.message || '')
          }
        } catch (error) {
          console.error(':', error)
          let errorMsg = ''
          if (error.response && error.response.data) {
            errorMsg = error.response.data.message || errorMsg
          } else if (error.message) {
            errorMsg = error.message
          }
          this.$message.error(errorMsg)
        } finally {
          this.createLoading = false
        }
      })
    },

    showImportDialog () {
      this.importText = ''
      this.importDialogVisible = true
    },

    async handleImportAccounts () {
      if (!this.importText.trim()) {
        this.$message.warning('')
        return
      }

      this.importLoading = true

      try {
        // 
        const lines = this.importText.trim().split('\n')
        const accounts = []

        for (const line of lines) {
          const parts = line.split(',').map(part => part.trim())
          if (parts.length >= 2) {
            accounts.push({
              address: parts[0],
              coinType: parts[1],
              description: parts[2] || ''
            })
          }
        }

        if (accounts.length === 0) {
          this.$message.warning('')
          return
        }

        const response = await accountApi.importAccounts({ accounts })

        if (response.data.code === 200) {
          this.$message.success(` ${accounts.length} `)
          this.importDialogVisible = false
          this.fetchAccountList()
        } else {
          throw new Error(response.data.message || '')
        }
      } catch (error) {
        console.error(':', error)
        let errorMsg = ''
        if (error.response && error.response.data) {
          errorMsg = error.response.data.message || errorMsg
        } else if (error.message) {
          errorMsg = error.message
        }
        this.$message.error(errorMsg)
      } finally {
        this.importLoading = false
      }
    },

    async refreshBalance (account) {
      try {
        const response = await transactionApi.getBalance(account.address)
        if (response.data.code === 200) {
          // 
          const index = this.accountList.findIndex(item => item.address === account.address)
          if (index !== -1) {
            this.$set(this.accountList[index], 'balance', response.data.data.balance)
            this.applyFilters() // 
          }
          this.$message.success('')
        }
      } catch (error) {
        console.error(':', error)
        this.$message.error('')
      }
    },

    viewAccountDetail (account) {
      this.selectedAccount = account
      this.detailDialogVisible = true
    },

    copyAddress (address) {
      if (navigator.clipboard) {
        navigator.clipboard.writeText(address).then(() => {
          this.$message.success('')
        }).catch(() => {
          this.fallbackCopyTextToClipboard(address)
        })
      } else {
        this.fallbackCopyTextToClipboard(address)
      }
    },

    fallbackCopyTextToClipboard (text) {
      const textArea = document.createElement('textarea')
      textArea.value = text
      document.body.appendChild(textArea)
      textArea.focus()
      textArea.select()
      try {
        document.execCommand('copy')
        this.$message.success('')
      } catch (err) {
        this.$message.error('')
      }
      document.body.removeChild(textArea)
    },

    async deleteAccount (account) {
      try {
        const confirm = await this.$confirm(
          ` "${this.formatAddress(account.address)}" `,
          '',
          {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }
        )

        if (confirm) {
          const response = await accountApi.deleteAccount(account.id)
          if (response.data.code === 200) {
            this.$message.success('')
            this.fetchAccountList() // 
          } else {
            throw new Error(response.data.message || '')
          }
        }
      } catch (error) {
        if (error !== 'cancel') {
          console.error(':', error)
          let errorMsg = ''
          if (error.response && error.response.data) {
            errorMsg = error.response.data.message || errorMsg
          } else if (error.message) {
            errorMsg = error.message
          }
          this.$message.error(errorMsg)
        }
      }
    },

    refreshAccountList () {
      this.searchAddress = ''
      this.filterCoinType = ''
      this.fetchAccountList()
    },

    formatAddress (address) {
      if (!address || address.length <= 20) return address
      return `${address.slice(0, 10)}...${address.slice(-8)}`
    },

    getCoinTypeTagType (coinType) {
      const typeMap = {
        ETH: 'primary',
        BTC: 'warning',
        USDT: 'success'
      }
      return typeMap[coinType] || 'info'
    }
  },
  watch: {
    searchAddress () {
      if (!this.searchAddress.trim()) {
        this.applyFilters()
      }
    }
  }
}
</script>

<style scoped>
.accounts-container {
    padding: 20px;
}

.search-area {
    margin-bottom: 20px;
}

.empty-state {
    text-align: center;
    padding: 40px 0;
    color: #606266;
}

.dialog-footer {
    text-align: right;
}

.import-area {
    margin-bottom: 20px;
}

.import-area p {
    margin: 10px 0;
    color: #606266;
}

.import-area .example {
    color: #909399;
    font-size: 12px;
}

.account-detail {
    padding: 10px 0;
}
</style>
